import { getPageListData } from '@/service/main/system/system'
import { Module } from 'vuex'
import { IRootState } from '../type'
import { ISystemState } from './type'

const systemModule: Module<ISystemState, IRootState> = {
  namespaced: true,
  state() {
    return {
      userList: [],
      userCount: 0,
      roleList: [],
      roleCount: 0,
      menuList: [],
      menuCount: 0,
      departmentList: [],
      departmentCount: 0
    }
  },
  getters: {
    pageListData(state: any) {
      return (pageName: string) => {
        return state[`${pageName}List`]
        // switch (pageName) {
        //   case 'user':
        //     return state.userList
        //   case 'role':
      }
    }
  },
  mutations: {
    changeUserList(state, userList: any[]) {
      state.userList = userList.sort(function (a: any, b: any) {
        return a.id - b.id
      }) // 按照id排序
    },
    changeUserCount(state, userCount: number) {
      state.userCount = userCount
    },
    changeRoleList(state, list: any[]) {
      state.roleList = list.sort(function (a: any, b: any) {
        return a.id - b.id
      })
    },
    changeRoleCount(state, count: number) {
      state.roleCount = count
    },
    changeMenuList(state, list: any[]) {
      state.menuList = list.sort(function (a: any, b: any) {
        return a.id - b.id
      })
    },
    changeMenuCount(state, count: number) {
      state.menuCount = count
    },
    changeDepartmentList(state, list: any[]) {
      state.departmentList = list.sort(function (a: any, b: any) {
        return a.id - b.id
      })
    },
    changeDepartmentCount(state, count: number) {
      state.departmentCount = count
    }
  },
  actions: {
    async getPageListAction({ commit }, payload: any) {
      const pageName = payload.pageName
      // 对页面发送请求
      const pageUrl = `${pageName}/list`
      // let pageUrl = ''
      // switch (pageName) {
      //   case 'user':
      //     pageUrl = 'user/list'
      //     break
      //   case 'role':
      //     pageUrl = 'role/list'
      //     break

      const pageResult: any = await getPageListData(pageUrl, payload.queryInfo)

      const { list, totalCount } = pageResult.data

      console.log(list, totalCount)

      commit(
        `change${pageName.charAt(0).toUpperCase() + pageName.slice(1)}List`,
        list
      )
      commit(
        `change${pageName.charAt(0).toUpperCase() + pageName.slice(1)}Count`,
        totalCount
      )
    }
  }
}

export default systemModule
